<template>
	<view>
		
			<view class="cu-form-group">
				<view class="title">标题</view>
				<input type="text" placeholder="请输入标题" v-model="biaoti" />
			</view>
			
			<view class="cu-form-group">
				<view class="title">姓名</view>
				<input type="text" placeholder="请输入姓名" v-model="biaoti" />
			</view>
			
			<view class="cu-form-group">
				<view class="title">手机</view>
				<input type="number" placeholder="请输入手机" v-model="biaoti" />
			</view>
		
			<view class="cu-form-group align-start" style="height: 50vw;">
				<view class="title">描述</view>
				<textarea style="height: calc(50vw - 10vw);" maxlength="-1" @input="textareaBInput" placeholder="填写信息描述"></textarea>
			</view>
		
			<view class="cu-bar bg-white margin-top">
				<view class="action">图片上传</view>
				<view class="action">{{ imgList.length }}/5</view>
			</view> 
			<view class="cu-form-group">
				<view class="grid col-4 grid-square flex-sub">
					<view class="bg-img" v-for="(item, index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
						<image :src="imgList[index]" mode="aspectFill"></image>
						<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index"><text class="cuIcon-close"></text></view>
					</view>
					<view class="solids" @tap="ChooseImage" v-if="imgList.length < 5"><text class="cuIcon-cameraadd"></text></view>
				</view>
			</view>
			
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgList: [],
				xiangxi_miaoshu:"",
				biaoti:"",
			}
		},
		onLoad() {

		},
		methods: {
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '提示',
					content: '确定要删除吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1);
						}
					}
				});
			},
			ChooseImage() {
				uni.chooseImage({
					count: 5, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: res => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths);
						} else {
							this.imgList = res.tempFilePaths;
						}
					}
				});
			},
			textareaBInput(e) {
				this.xiangxi_miaoshu = e.detail.value;
			},
		}
	}
</script>

